


import { StatusBar } from 'expo-status-bar';
import { useState } from 'react';
import { StyleSheet, View, TouchableOpacity, Text, } from 'react-native';
import { TextInput, } from 'react-native-web';

const User = () => {
  const [input1, setInput1] = useState('');
  const [input2, setInput2] = useState('');
  const [result, setResult] = useState(null)

    const Calculation = (operation) => {
        const num1 =   parseFloat(input1)
         const num2 =  parseFloat(input2)

         if(isNaN(num1) || isNaN(num2)) {
           setResult('please enter an valid number')
           setInput1('')
           setInput2('')

           return;
         }

         let calculation;
         switch(operation) {
          case "+":
            calculation = num1 + num2;
            break;
            case "-":
              calculation = num1 - num2;
              break;
              case "*":
                calculation = num1 * num2;
                break;
                case "/":
                  calculation = num2 !== 0 ? num1 / num2 : 'Cannot divide by Zero';
                  break;
                  default: 
                  calculation = 'invalid operation'
         }
            setResult(calculation)
    }
  

  return (

     
     <View style={styles.container}>

        <Text style={styles.title}>React Native Calculator</Text> 
          <TextInput style={styles.input} onChangeText={setInput1} keyboardType="numeric" value={input1} placeholder='User enter number'></TextInput>
          <TextInput style={styles.input} onChangeText={setInput2} keyboardType="numeric" value={input2} placeholder='User enter second number'></TextInput>

          <View style={styles.buttonContainer}>
            <TouchableOpacity style={styles.button} onPress={() => Calculation('+')}>
              <Text style={styles.buttonText}> + </Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.button} onPress={() => Calculation('-')}>
              <Text style={styles.buttonText}> - </Text>
            </TouchableOpacity>

            <TouchableOpacity style={styles.button} onPress={() => Calculation('*')}>
              <Text style={styles.buttonText}> ร </Text>
            </TouchableOpacity>

            <TouchableOpacity style={styles.button} onPress={() => Calculation('/')}>
              <Text style={styles.buttonText}> รท </Text>
            </TouchableOpacity>

          </View>

          { result !== null && (
            <Text style={styles.result}> Result: { result } </Text>
          )}

     </View>

     


   
  );
};

export default User;




 const styles = StyleSheet.create({
  container: {
    display:'flex',
    alignItems:'center',
    justifyContent:'center',
    flex:1,
    padding:10
  },
  title: {
    fontSize:24,
    fontWeight:'bold',
    marginBottom: 20,
  },
  input: {
    width: '80%',
    height:40,
    borderWidth:3,
    borderColor: 'green',
    borderRadius:15,
    marginBottom: 15,
    paddingHorizontal:10,
    fontSize:18,
    textAlign: 'center'
  },
  buttonContainer: {
    flexDirection:'row',
    justifyContent: 'space-around',
    width: '80%',
    marginBottom:20
  },
  button: {
    backgroundColor: 'purple',
    padding: 10,
    borderRadius: 5,
    width: 50,
    alignItems: 'center',
  },
  buttonText: {
    color: 'white',
    fontSize: 18,
    fontWeight: 'bold',
  },
  result: {
    fontSize: 20,
    marginTop: 20,
    color: 'green',
  },

 })